.App{
    position: absolute;
    background: #F5F5F5;
    padding: 20px;
    border-radius: 10px;
    height: 95%;
    width: 95%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.die-box{
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 10px;
}
.dice{
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 7px;
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
h2{
    color: #2B283A;
    font-weight: 700;
    font-size: 2rem;
}
.roll-button{
    background: #5035FF;
    box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
    border-radius: 4px;
    padding: 10px;
    border: none;
    width: 25%;
    color: white;
    font-size: 1.5rem;
    margin-top: 20px;
}
.roll-button:focus {
    outline: none;
}

.roll-button:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
.header{
    color: #2B283A;
    font-size: 50px;
    font-weight: 700;
    margin: 0;
}
.paragraph{
    font-size: 26px;
    color: #4A4E74;
    font-weight: 400;
    text-align: center;
}
@media screen and (max-width: 800px) {
    .roll-button{
        width: 60%;
    }
    .dice{
        height: 45px;
        width: 45px;
    }
}